import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import sr from '@utils/sr';
import { srConfig } from '@config';
import styled from 'styled-components';
import { theme, mixins, media, Section, Heading } from '@styles';

const { colors, fontSizes, fonts } = theme;

const StyledContainer = styled(Section)`
  position: relative;
  max-width: 900px;
`;

const StyledFlexContainer = styled.div`
  display: flex;
  align-items: flex-start;

  ${media.tablet`
    display: block;
  `}
`;

const StyledTabList = styled.ul`
  position: relative;
  z-index: 3;
  width: max-content;
  padding: 0;
  margin: 0;
  list-style: none;

  ${media.tablet`
    display: flex;
    overflow-x: auto;
    margin-bottom: 30px;
  `}
`;

const StyledTabButton = styled.button`
  ${mixins.link};
  background-color: transparent;
  border: 0;
  border-left: 2px solid ${colors.lightestNavy};
  padding: 0 20px;
  height: ${theme.tabHeight}px;
  font-family: ${fonts.SFMono};
  font-size: ${fontSizes.smish};
  color: ${({ isActive }) => (isActive ? colors.green : colors.slate)};
  transition: ${theme.transition};
  text-align: left;
  width: 100%;
  // white-space: nowrap;
  white-space: normal;
  word-break: break-word;
  overflow: hidden;
  text-overflow: ellipsis;

  ${media.tablet`
    border-left: none;
    border-bottom: 2px solid ${colors.lightestNavy};
    min-width: 120px;
    padding: 10px;
    text-align: center;
  `};

  &:hover {
    background-color: ${colors.lightNavy};
  }
`;

const StyledHighlight = styled.span`
  background: ${colors.green};
  width: 2px;
  height: ${theme.tabHeight}px;
  border-radius: ${theme.borderRadius};
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(${({ activeTabId }) => activeTabId * theme.tabHeight}px);
  transition: transform 0.25s ease-in-out;

  ${media.tablet`
    width: 100%;
    height: 2px;
    top: auto;
    bottom: 0;
    transform: translateX(${({ activeTabId }) => activeTabId * theme.tabWidth}px);
  `}
`;

const StyledContent = styled.div`
  width: 100%;
  padding-left: 30px;

  ${media.tablet`padding-left: 0;`}
`;

const StyledDetails = styled.div`
  h4 {
    font-size: ${fontSizes.xl};
    color: ${colors.lightestSlate};
    font-weight: 600;
    margin-bottom: 5px;

    span {
      font-weight: normal;
      color: ${colors.green};
    }

    a {
      ${mixins.inlineLink};
    }
  }

  .range {
    font-family: ${fonts.SFMono};
    font-size: ${fontSizes.smish};
    color: ${colors.slate};
    margin-bottom: 20px;
  }

  ul {
    ${mixins.fancyList};
  }
`;

const Extracurricular = ({ data }) => {
  const [activeTabId, setActiveTabId] = useState(0);
  const [tabFocus, setTabFocus] = useState(null);
  const tabs = useRef([]);
  const revealContainer = useRef(null);

  useEffect(() => sr.reveal(revealContainer.current, srConfig()), []);
  useEffect(() => {
    if (tabFocus !== null && tabs.current[tabFocus]) tabs.current[tabFocus].focus();
  }, [tabFocus]);

  const activities = data[0].node.frontmatter.items;

  const onKeyPressed = e => {
    if (e.keyCode === 38) setTabFocus(tabFocus - 1);
    if (e.keyCode === 40) setTabFocus(tabFocus + 1);
  };

  return (
    <StyledContainer id="extracurricular" ref={revealContainer}>
      <Heading>Extracurricular</Heading>
      <StyledFlexContainer>
        <StyledTabList role="tablist" aria-label="Extracurricular tabs" onKeyDown={onKeyPressed}>
          {activities.map((item, i) => (
            <li key={i}>
              <StyledTabButton
                isActive={activeTabId === i}
                onClick={() => setActiveTabId(i)}
                ref={el => (tabs.current[i] = el)}
                id={`tab-${i}`}
                role="tab"
                aria-selected={activeTabId === i}
                aria-controls={`panel-${i}`}
                tabIndex={activeTabId === i ? '0' : '-1'}>
                {item.institution}
              </StyledTabButton>
            </li>
          ))}
          <StyledHighlight activeTabId={activeTabId} />
        </StyledTabList>

        <StyledContent>
          {activities.map((item, i) => (
            <div
              key={i}
              hidden={activeTabId !== i}
              id={`panel-${i}`}
              role="tabpanel"
              aria-labelledby={`tab-${i}`}
              tabIndex="0">
              <StyledDetails>
                <h4>
                  {item.designation}
                  <span style={{ color: colors.green }}> @</span>
                  <br />
                  {item.url ? (
                    <a href={item.url} target="_blank" rel="noopener noreferrer">
                      {item.institution}
                    </a>
                  ) : (
                    item.institution
                  )}
                </h4>


                <p className="range">{item.period}</p>
                {item.responsibilities && (
                  <ul>
                    {item.responsibilities.map((res, j) => (
                      <li key={j}>{res}</li>
                    ))}
                  </ul>
                )}
              </StyledDetails>
            </div>
          ))}
        </StyledContent>
      </StyledFlexContainer>
    </StyledContainer>
  );
};

Extracurricular.propTypes = {
  data: PropTypes.array.isRequired,
};

export default Extracurricular;